@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.css);
html,
body,
* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat-Regular';
}

@font-face {
  font-family: 'Montserrat-Light';
  src: url(/static/media/Montserrat-Light.409c7f79.ttf);
}

@font-face {
  font-family: 'Montserrat-Regular';
  src: url(/static/media/Montserrat-Regular.ee653992.ttf);
}

@font-face {
  font-family: 'Montserrat-Medium';
  src: url(/static/media/Montserrat-Medium.c8b6e083.ttf);
}

@font-face {
  font-family: 'Montserrat-SemiBold';
  src: url(/static/media/Montserrat-SemiBold.c641dbee.ttf);
}

.my-loader {
  text-align: center;
}

.green {
  color: #1cbe88;
}

.tiny {
  font-size: 0.5rem;
}

.little {
  font-size: 1rem;
}

.little p {
  font-size: 0.75rem;
}

.medium {
  font-size: 2rem;
}

.medium p {
  font-size: 1.25rem;
}

.large {
  font-size: 5rem;
}

.large p {
  font-size: 1.5rem;
}

.parent {
  max-width: 87.5%;
  margin: auto;
}

::-webkit-input-placeholder {
  color: #d8d8d8;
}

:-ms-input-placeholder {
  color: #d8d8d8;
}

::-ms-input-placeholder {
  color: #d8d8d8;
}

::placeholder {
  color: #d8d8d8;
}

.logo-text {
  font-size: 20px;
  color: black;
  font-weight: bold;
}

.logo-text:hover {
  color: black;
  text-decoration: none;
}

.guest-hero {
  width: 100%;
  height: 80vh;
  position: relative;
  display: flex;
}

.hero {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
}

.hero-image {
  position: absolute;
  right: 0;
  top: 0;
}

.second {
  height: 60vh;
  margin: auto;
}

.search-input {
  border: 0px;
  border-bottom: 1px solid #d8d8d8;
  width: 30vw;
  height: 5vh;
  font-size: 20px;
  transition: all 0.3s ease-in-out;
}

.search-input:focus {
  border: 0px;
  border-bottom: 1px solid black;
  outline: none;
}

.hero-wrapper {
  margin-top: 10%;
  margin-left: 14%;
}

.moto {
  font-size: 34px;
}

.services-text {
  font-size: 30px;
}

.card-wrapper {
  display: flex;
  justify-content: space-between;
  height: 37vh;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: row;
}

.navbar {
  background-color: white;
  border: 0px;
  height: 10vh;
}

.logo {
  color: black;
  font-size: 1.4rem;
  font-weight: bolder;
}

.logo:hover {
  text-decoration: none;
  color: black;
}

.link {
  color: black;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.link:not(:last-child) {
  margin-right: 2.5vw;
}

.link:hover {
  color: #1cbe88;
  text-decoration: none;
  cursor: pointer;
}

.register-link {
  height: 5vh;
  line-height: 5vh;
  text-align: center;
  width: 4vw;
  color: white;
  background-color: #1cbe88;
  border: 1px solid #1cbe88;
  transition: all 0.3s ease;
  border-radius: 2px;
}

.register-link:hover {
  text-decoration: none;
  color: #1cbe88;
  background-color: white;
  border: 1px solid #1cbe88;
  border-radius: 2px;
}

.profile-image {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
}

.header-submenu {
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 0;
  top: 40px;
  height: 25vh;
  width: 15vw;
  z-index: 9999;
  background-color: white;
  justify-content: space-around;
  border: 1px solid #c9c9c9;
  border-radius: 2px;
}

.header-submenu-item {
  color: black;
  transition: all 0.2s ease;
  padding-left: 10%;
}

.header-submenu-item:hover {
  text-decoration: none;
  color: #1cbe88;
}

.divider {
  height: 0.5px;
  width: 85%;
  margin: 0 auto;
  background-color: #c9c9c9;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  color: #757575;
}

.submenu {
  display: none;
  position: absolute;
}

.parent-li {
  position: relative;
  margin-top: 2.5%;
  cursor: pointer;
  color: #757575;
}

.parent-li:last-child .submenu {
  right: 0;
}

.parent-li:hover {
  text-decoration: underline;
  -webkit-text-decoration-color: #757575;
          text-decoration-color: #757575;
  color: #757575;
}

.parent-li:hover .submenu {
  display: block;
  background-color: white;
  z-index: 1000;
  width: 30vw;
  height: 40vh;
  border: 0.5px solid #d8d8d8;
}

.subitem {
  display: inline-block;
  width: 50%;
  font-size: 1.1rem;
  color: #757575;
  cursor: pointer;
}

.subitem:hover {
  text-decoration: none;
  color: #757575;
}

.card-container {
  position: relative;
  height: 40vh;
  width: 15vw;
  border-radius: 2.5%;
  transition: all 0.3s ease-in;
}

.card-container:hover {
  -webkit-filter: brightness(107.5%);
          filter: brightness(107.5%);
}

.card-image {
  width: 100%;
  height: 100%;
}

.card-description {
  position: absolute;
  color: white;
  left: 5%;
  top: 5%;
  font-size: 16px;
}

.card-name {
  position: absolute;
  color: white;
  left: 5%;
  top: 12%;
  font-size: 24px;
  font-weight: bold;
}

.pagination {
  width: 100%;
  justify-content: start;
}

/* category page */
.hero {
  display: flex;
  width: 100%;
  height: 100vh;
}

.subcategory-container {
  width: 17.5vw;
}

.services-container {
  width: 82.5vw;
  height: 155vh;
}

.inner-service-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.subcategory-list {
  margin: 0;
}

.category-main-title {
  font-size: 1.25rem;
}

.category-main-description {
  font-size: 1rem;
  color: #7a7a7a;
}

.subcategory-name {
  font-size: 1.1rem;
  color: #adadad;
  margin-bottom: 2%;
}

.subcategory-name:hover {
  color: #adadad;
}

.center-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
  width: 100%;
}

/* service component */
.service-container {
  height: 40vh;
  width: 20vw;
  border: 1px solid #bababa;
  margin-bottom: 5%;
  border-radius: 3px;
}

.service-image {
  height: 30vh;
  width: 100%;
}

.service-details-container {
  padding-left: 2.5%;
  width: 90%;
}

.service-container:hover {
  text-decoration: none;
  color: #1cbe88;
}

.user-name {
  margin: 0;
  font-size: 14px;
  color: black;
}

.user-name:hover {
  color: black;
}

.service-name {
  margin: 0;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
  transition: all 0.3s ease;
}

.service-price {
  margin: 0;
}

/* service screen */
.gig-container {
  margin-top: 5%;
  width: 100%;
  height: 70vh;
  display: flex;
  justify-content: space-around;
}

.gig-container-loading {
  width: 100%;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.gig-left-container {
  height: 70vh;
}

.current-service-image {
  width: 100%;
  height: 62vh;
}

.gig-right-container {
  height: 70vh;
  width: 25%;
  border: 1px solid #bababa;
  border-radius: 1px;
}

.wrapper {
  display: flex;
}

.first-half {
  background-color: whitesmoke;
  width: 40%;
  height: 100vh;
  text-align: center;
}

.second-half {
  background-color: #1cbe88;
  width: 60%;
  height: 100vh;
}

.login-text {
  color: #1cbe88;
  font-size: 3.5rem;
  font-weight: 500;
  margin-top: 20%;
}

.welcome-text {
  color: white;
  font-size: 22px;
  font-weight: lighter;
  margin-top: 35%;
  margin-left: 25%;
}

.flex-text {
  color: white;
  margin-left: 25%;
  font-size: 60px;
  font-weight: bold;
}

.white-line {
  margin-left: 25%;
  margin-top: 1.5%;
  height: 0.2%;
  width: 50%;
  background-color: white;
}

.desc-text {
  margin-left: 25%;
  margin-top: 1.5%;
  color: white;
}

.form-wrapper {
  margin: 0 auto;
  width: 50%;
}

.custom-input {
  background-color: whitesmoke;
  border: 0px;
  width: 60%;
  margin-top: 15%;
  border-bottom: 1px solid #1cbe88;
  transition: all 0.3s ease;
}

.custom-input:focus {
  outline: none;
  border-bottom: 1px solid #17966b;
}

.custom-input2 {
  background-color: whitesmoke;
  border: 0px;
  width: 60%;
  margin-top: 5%;
  border-bottom: 1px solid #1cbe88;
  transition: all 0.3s ease;
}

.custom-input2:focus {
  outline: none;
  border-bottom: 1px solid #17966b;
}

.login-button {
  width: 30%;
  height: 5vh;
  background-color: whitesmoke;
  color: #1cbe88;
  border: 1px solid #1cbe88;
  border-radius: 3px;
  transition: all 0.3s ease;
}

.login-button:hover {
  color: white;
  background-color: #1cbe88;
  border: 1px solid #1cbe88;
}

.forgot-password {
  color: #1cbe88;
  font-weight: bold;
  margin-top: 2.5%;
  margin-bottom: 10%;
}

.error-message {
  color: #ff0000;
  margin: 0;
}

.hidden-message {
  color: whitesmoke;
  margin: 0;
}

